<template>
  <!--常见问题-->
  <div id="service2" class="left">
    <home-header :title="'常见问题'" :back="true"></home-header>
    <div class="service-content">
      <div class="title">
        1.什么是加油卡
      </div>
      <div>
        加油卡是用来为汽车加油的储值卡，有极高的便利性，尤其是在不支持银行卡付款的地区。不用下车将卡给加油员即可加油，且在备有自助加油机的加油站中，在没有员工情况下，刷卡即可出油。
      </div>
      <div class="title">
        2.关于加油卡办理
      </div>
      <div>
        线上办卡，鑫彩支持在线购买1000元面值的加油储值卡，余额使用完后支持后续充值（限鑫彩平台）。在购卡页面可进行办理。
      </div>
      <div class="title">
        3.关于圈存
      </div>
      <div>
        “圈存”就是将您网上充值的金额，或主卡预分配给加油卡副卡的资金，存入到加油卡的IC卡芯片中。未进行圈存，资金只是进入了系统后台的备付金账户，加油卡上还没有；圈存后，激活已充值到账的金额到加油卡中，系统后台和加油卡上均有相同资金，可以进行刷卡加油。加油卡圈存可至所属加油站请工作人员帮忙操作。
      </div>
      <div class="title">
        4.预分配
      </div>
      <div>
        如果您是中石化卡，充值到账但圈存时无余额，则可能是因为加油卡为多用户卡，需登录中石化官网或请加油站工作人员帮忙给加油卡“预分配”，然后进行圈存使用。
      </div>
      <div class="title">
        5.充值限制
      </div>
      <div>
        每个用户单个自然日内可充值5次，单次最高充值金额为1000元。互联鑫彩平台办理的加油储值卡无限制。
      </div>
      <div class="title">
        6.系统维护时间充值
      </div>
      <div>
        1).鑫彩专享卡：17:30-9:00及节假日为系统维护时间，暂不支持充值服务，在此期间支付将延时为您处理；
      </div>
      <div>
        2).中石化实名卡：22:00—2:00为系统维护时间，在此期间支付将延迟至次日10:00到账；
      </div>
      <div class="title">
        7.关于发票
      </div>
      <div>
        线上加油卡代充值，暂不支持开具发票。
      </div>
      <div class="title">
        8.关于充值
      </div>
      <div>
        暂不支持中石油实名卡充值。
      </div>
    </div>
  </div>
</template>

<script>
  import homeHeader from '../../components/header/header.vue'

  export default {
    data() {
      return {}
    },
    computed: {},
    components: {
      homeHeader
    },
    methods: {},
    mounted() {

    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/minxin";

  .service-content {
    padding: $top 1rem $bottom;
    div {
      @include sh($min, 1.85rem);
      text-indent: 2em;
    }
    .title {
      @include sh($med, ($med * 2));
      text-indent: $min * 2;
      color: #000;
      font-weight: 600;
    }
  }
</style>



